@import url(https://fonts.googleapis.com/css2?family=Anonymous+Pro&display=swap);

.body {
  font-family: 'Anonymous Pro', monospace;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
}

.container {
  position: relative;
  font-family: 'Anonymous Pro', monospace;
  font-weight: 400;
  font-style: normal;
}

.food_title {
  text-align: center;
  font-family: 'Anonymous Pro', monospace;
  font-weight: 400;
  font-style: normal;
  font-size: 40px;
}

.Ingingredients {
  font-family: 'Anonymous Pro', monospace;
  font-weight: 400;
  font-style: normal;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: relative;
  border: 2px solid #000;
  padding: 20px;
  margin: 20px;
  height: 150px;
}

.Results {
  font-family: 'Anonymous Pro', monospace;
  font-weight: 400;
  font-style: normal;
  position: relative;
  border: 2px solid #000;
  padding: 20px;
  margin: 20px;
}

input {
  font-family: 'Anonymous Pro';
  font-weight: 400;
  font-style: normal;
  outline: 0;
  border-width: 0 0 2px;
  border-color: black;
  text-align: center;
  position: relative;
  width: 300px;
  margin: 20px;
  line-height: 4ex;
  font-size: 25px;

}

.input_label {
  font-weight: bold;
  font-size: 25px;
}


.Ingingredient_label {
  position: absolute;
  top: -10px;
  left: 30px;
  background-color: white;
  padding: 0 10px;
  font-weight: bold;
  font-size: 30px;
}

p {
  font-family: 'Anonymous Pro', monospace;
  font-weight: 400;
  font-style: normal;
  font-size: 25px;
  text-align: center;
}

.Result_label {
  position: absolute;
  top: -10px;
  left: 30px;
  background-color: white;
  padding: 0 10px;
  font-weight: bold;
  font-size: 30px;
}

.check {
  border: 2px solid black;
  background-color: white;
  color: black;
  padding: 10px 20px;
  font-size: 20px;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;
}

.check:hover {
  background-color: black;
  color: white;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin: 20px 0;
}

th,
td {
  padding: 8px 12px;
  border: 1px solid #0b0b0b;
  font-size: 20px;
}

th {
  background-color: #0d0d0d;
  color: white;
}